<template>
  <div id="article-container">
    <article class="story" v-for="item in feed" :key="item.title">
      <a class="str-pic" :href="item.link">
        <figure><img :src="item.img" :alt="item.title"></figure>
      </a>
      <p class="str-date">Date: {{ item.date }}</p>
      <a class="str-title" :href="item.link"><h1>{{ item.title }}</h1></a>
      <p class="str-author">{{ item.author }}</p>
      <a class="str-des" :href="item.link"><p>{{ item.description }}</p></a>
    </article>
  </div>
</template>

<script>
const axios = require('axios');
import Mercury from '@postlight/mercury-parser';
export default {
  name: 'lolo',
  data() {
    return {
      feed: []
    }
  },
  created: function () {
    let self = this;
    axios.get('https://cors-anywhere.herokuapp.com/https://flipboard.com/@raimoseero/feed-nii8kd0sz.rss', {
      responseType: 'document'
    })
      .then(function (response) {
        let rssFeed = response.data.getElementsByTagName('item');
        for (let item of rssFeed) {
          let strTitle = item.getElementsByTagName('title')[0] == null ? '' : item.getElementsByTagName('title')[0].innerHTML.substr(0,130);
          let strLink = item.getElementsByTagName('link')[0] == null ? '' : item.getElementsByTagName('link')[0].innerHTML;
          let strPubDate = item.getElementsByTagName('pubDate')[0] == null ? '' : item.getElementsByTagName('pubDate')[0].innerHTML;
          let strAuthor = item.getElementsByTagName('author')[0] == null ? 'Unknown' : item.getElementsByTagName('author')[0].innerHTML;
          let strDescription = item.getElementsByTagName('description')[0];
          if (strDescription == null) {
            strDescription = ''
          } else {
            strDescription = item.getElementsByTagName('description')[0].innerHTML.replace(/&amp;lt;p&amp;gt;|&amp;lt;\/b&amp;gt;|&amp;lt;b&amp;gt;/g, "").substr(0,200) + "...";
          }
          let strImg = item.getElementsByTagName('media:content')[0] == null ? 'https://raw.githubusercontent.com/deltora72/slot-game/master/bg-theme.jpg' : item.getElementsByTagName('media:content')[0].getAttribute('url')

          self.feed.push({
            title: strTitle,
            link: strLink,
            date: strPubDate,
            author: strAuthor,
            description: strDescription,
            img: strImg,
          });
        }
      });
  },
  methods: {
    fetchFeed: function() {
      let url="https://cors-anywhere.herokuapp.com/https://www.gamespot.com/gallery/slug/2900-337";
      Mercury.parse(url).then(result => console.log(result));
    }
  }
}
</script>

